<template >
    <div>
        <div v-if="dataList.length<10" >
            <table-item  v-for="(item,index) in dataList" :key="index" :index="index" 
                :width-array="widthArray" :childenname="childenname" :name="name" :slotMap ="slotMap"
                :step="step" :left="left" :keys="keys"
                :item ="item"  >
            </table-item>
        </div>
        
        <div class="table-page" v-else>
            <table-item  v-if="pageList.length>0" v-for="(item,index) in pageList" :key="index" :index="index" 
                :width-array="widthArray" :childenname="childenname" :name="name" :slotMap ="slotMap"
                :step="step" :left="left" :keys="keys"
                :item ="item"  >
            </table-item>

            <el-pagination
            :current-page.sync="pageIndex"
            :page-size="pageNum"
            @current-change="init"
            layout="total,prev, pager, next"
            :total="dataList.length">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import store from './store'
import {Pagination} from 'element-ui'
import tableItem from './table-item.js'
import Vue from 'vue'
Vue.use(Pagination)

export default {
    name:'tableBody',
    store,
    props:{
        slotMap:{
            type:Object,
            default:function(){
                return {}
            }
        },
        left:{
            type:Number,
            default:5
        },
        step:{
            required:true
        },
        pid:{
            required: true
        },
        childenname:{
            type:String,
            default:'children'
        },
        id:{
            type:String,
            default:'id'
        },
        //id
        rootKey:{
            type:String,
            default:''
        },
        //
        name:{
            type:String,
            default:'name'
        },
        //
        parentKey:{
            type:String,
            default:'pid'
        },
        dataList:{
            type:Array,
            default:function(){
                return  []
            }
        },
        widthArray:{
            type:Array,
            default:function(){
                return  []
            }
        },
        keys:{
            type:Array,
            default:function(){
                return  []
            }
        },
    },
    watch:{
        dataList:{
            handler(newVal,oldVal){
                this.pageIndex =1;
                this.init();
            },
            deep:true
        }
    },
    components:{tableItem},
    data(){
        return{
            pageIndex:1,
            childList:[],
            pageList:[],
            pageNum:10
        }
    },
    methods:{
        init(num){
            var index =num==null?this.pageIndex:num;
            var page =this.pageNum;
            if(this.dataList.length>page){
                var arr =[];
                var start =(index-1) * page;
                for(var i =0;i<page;i++){
                    if(start+i<this.dataList.length){
                        arr.push(this.cloneObj(this.dataList[start+i]));
                    }
                }
                this.$set(this,'pageList',arr);
            }
        },
        cloneObj(obj) {
            var newObj = {};
            if (obj instanceof Array) {
                newObj = [];
            }
            for (var key in obj) {
                var val = obj[key];
                newObj[key] = (val!==null && typeof val === 'object') ? this.cloneObj(val) : val;
            }
            return newObj;
        },
    },
    mounted(){
        this.pageNum =this.$store.state.pageNum;
        this.init();
        

    }
}
</script>

